.Movie_movie__25for {
  background-color: white;
  font-weight: 300;
  color: #adaeb9;
  color: inherit;
  display: grid;
  margin-bottom: 70px;
  padding: 20px;
  font-size: 13px;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
  text-decoration: none;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.Movie_movie_img__REZGD {
  position: relative;
  box-shadow: 2px 2px 10px black;
  max-width: 150px;
  width: 100%;
  top: -50px;
  margin-right: 30px;

  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.Movie_movie_title__1rhRc,
.Movie_movie_year__3lCz7 {
  margin: 0;
  font-weight: 300;
  text-decoration: none;
}

.Movie_movie_title__1rhRc a {
  margin-bottom: 5px;
  font-size: 24px;

  text-decoration: none;
  color: #2c2c2c;
}

.Movie_movie_genres__3isEx {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}
.Movie_movie_genres__3isEx li,
.Movie_movie_year__3lCz7 {
  margin-right: 10px;
  font-size: 14px;
}

.Home_movies__gZbrP {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

@media screen and (max-width: 1080px) {
  .Home_movies__gZbrP {
    display: grid;
    grid-template-columns: 1fr;
    width: 100%;
  }
}
.Home_loader__x-dEv {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.Home_container__2jdYY {
  justify-content: center;
  height: 100%;
  display: flex;
}

